@font-face {
  font-family: "Garamond";
  src: url("../fonts/Garamond.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato-Light";
  src: url("../fonts/Lato-Light.woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Garamond", sans-serif;
  font-weight: 400;
  font-size: 23px;
  line-height: 20px;
  color: #161616;
}
body.lock {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  max-width: 1150px;
  padding: 0 15px;
}

.btn-reset {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: inherit;
}

.link-reset {
  text-decoration: none;
  color: inherit;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.input {
  all: unset;
  margin-bottom: 30px;
  padding: 10px 10px 10px 10px;
  width: 100%;
  background-color: #FEFEFE;
  font-family: "Lato-Light", sans-serif;
  font-weight: 300;
  font-size: 14px;
  line-height: 17px;
}

.button {
  padding: 18px 38px;
  font-size: 20px;
  line-height: 22px;
  color: #F6F6F5;
  background-color: #69766D;
  border: 2px solid #69766D;
  transition: all 0.4s;
}
.button:hover {
  color: #69766D;
  background-color: inherit;
}

body.lock {
  overflow: hidden;
}

.header {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  padding: 25px 0;
}
.header__container {
  max-width: 1140px;
}
.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 805px;
  width: 100%;
}

.logo {
  position: relative;
  width: 117px;
  height: 79px;
  z-index: 5;
}
.logo__img {
  width: 100%;
  height: 100%;
}

.burger {
  display: none;
}

.menu__item {
  position: relative;
  font-size: 20px;
  line-height: 22px;
}
.menu__item::after {
  position: absolute;
  content: "";
  width: 0;
  border-top: 2px dashed #161616;
  top: 100%;
  right: 0;
  transition: all 0.4s;
}
.menu__item:hover::after {
  width: 100%;
  left: 0;
}
.menu__item:nth-child(1)::before {
  position: absolute;
  content: "";
  width: 27px;
  border-top: 1px solid #D8BFA9;
  top: -2px;
  left: 0;
}

/*-------------------------*/
body.touch .menu__link {
  flex: 1 1 auto;
  transition: transform 0.4s ease 0s;
}

/*-------------------------*/
.contact {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 185px;
  width: 100%;
}
.contact:hover .contact__number::after {
  width: 100%;
  right: 0;
}
.contact__icon {
  width: 20px;
  height: 20px;
}
.contact__number {
  font-size: 17px;
  line-height: 22px;
  color: #B8926B;
  position: relative;
}
.contact__number::after {
  position: absolute;
  content: "";
  width: 0;
  border-top: 2px solid #B8926B;
  top: 102%;
  left: 50%;
  transition: width 0.4s;
  transform: translateX(-50%);
}

@media screen and (min-width: 940px) {
  .menu {
    display: flex;
    align-items: center;
  }
  .menu__item {
    margin-right: 30px;
    font-size: 20px;
  }
  .menu__item:last-child {
    margin-right: 0;
  }
}
@media screen and (max-width: 940px) {
  .burger {
    z-index: 5;
    display: block;
    width: 30px;
    height: 18px;
    position: relative;
    cursor: pointer;
  }
  .burger::before, .burger::after, .burger__line {
    position: absolute;
    left: 0;
    border-top: 1px solid #FEFEFE;
    width: 100%;
    transition: all 0.4s ease 0s;
    background-color: #FEFEFE;
  }
  .burger::before, .burger::after {
    content: "";
  }
  .burger::before {
    top: 0;
  }
  .burger::after {
    bottom: 0;
  }
  .burger__line {
    top: 50%;
    transform: scale(1) translate(0, -50%);
  }
  .burger.active::before {
    top: 50%;
    transform: rotate(-45deg) translate(0, -50%);
  }
  .burger.active::after {
    bottom: 50%;
    transform: rotate(45deg) translate(0, 50%);
  }
  .burger.active .burger__line {
    transform: scale(0) translate(0, -50%);
  }
  .nav {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.9);
    padding: 135px 30px 30px 30px;
    transition: left 0.4s ease 0s;
    overflow: auto;
  }
  .nav::before {
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 115px;
    background-color: #A52A2A;
    z-index: 2;
  }
  .nav.active {
    left: 0;
  }
  .menu__item {
    flex-wrap: wrap;
    margin-bottom: 30px;
    font-size: 24px;
  }
  .menu__item:last-child {
    margin-bottom: 0;
  }
  .menu__item:hover::after {
    width: 0;
  }
}
.hero {
  padding-top: 45px;
  padding-bottom: 25px;
}
.hero__wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.hero__representation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 80px;
}
.hero__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 570px;
  width: 100%;
  position: relative;
}
.hero__decoration {
  position: absolute;
  width: 356px;
  height: 210px;
  top: -100px;
  left: -75px;
}
.hero__title {
  margin: 0;
  margin-bottom: 30px;
  font-weight: 400;
  font-size: 59px;
  line-height: 66px;
}
.hero__text {
  margin: 0;
  margin-bottom: 65px;
  font-family: "Lato-Light", sans-serif;
  font-weight: 300;
  font-size: 24px;
  line-height: 34px;
}
.hero__inner-pictures {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.hero__inner-img {
  margin-right: 35px;
  max-width: 337px;
  position: relative;
}
.hero__inner-img::after {
  position: absolute;
  content: "";
  max-width: 318px;
  width: 100%;
  max-height: 434px;
  height: 100%;
  border: 2px solid #F6F6F5;
  border-radius: 410px 410px 0px 0px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.hero__img {
  width: 100%;
  height: auto;
  border-radius: 410px 410px 0px 0px;
}
.hero__icons {
  display: flex;
  flex-direction: column;
}
.hero__inner-icon {
  position: relative;
  margin-bottom: 30px;
}
.hero__inner-icon::after {
  position: absolute;
  content: "";
  width: 40px;
  height: 40px;
  border: 1px solid #D8BFA9;
  border-radius: 50%;
  top: -50%;
  left: -50%;
  transform: translate(5%, 10%);
}
.hero__inner-icon:last-child {
  margin-bottom: 8px;
}
.hero__icon {
  width: 23px;
  height: 23px;
}
.hero__steps {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.hero__caption {
  margin: 0;
  margin-bottom: 65px;
  font-weight: 400;
  font-size: 40px;
  line-height: 45px;
  position: relative;
}
.hero__caption::after {
  position: absolute;
  content: "";
  width: 69px;
  border-top: 2px solid #D8BFA9;
  top: -8px;
  left: 0;
}
.hero__inner-cards {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  max-width: 1110px;
  width: 100%;
  gap: 25px 50px;
}
.hero__card {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 175px;
  min-height: 260px;
  position: relative;
}
.hero__card::after {
  position: absolute;
  content: url(../../img/svg/steps-decoration-upper-arc.svg);
  width: 146px;
  height: 33px;
  top: -13px;
  left: 80px;
}
.hero__card:nth-child(even)::after {
  content: url(../../img/svg/steps-decoration-lower-arc.svg);
  top: 52px;
}
.hero__card:last-child::after {
  display: none;
}
.hero__inner-steps-icon {
  margin-left: 10px;
  margin-bottom: 60px;
  width: 60px;
  height: 60px;
}
.hero__steps-icon {
  width: 100%;
  height: auto;
}
.hero__heading {
  margin: 0;
  margin-bottom: 25px;
  font-weight: 400;
  font-size: 23px;
  line-height: 20px;
}
.hero__descr {
  margin: 0;
  font-family: "Lato-Light", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
}

.modal {
  position: fixed;
  left: 0;
  top: 0;
  overflow-x: hidden;
  overflow-y: auto;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 0;
  visibility: hidden;
  transition: all 0.8s ease 0s;
}
.modal__body {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 10px;
  min-height: 100%;
  transition: all 0.8s ease 0s;
}
.modal__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 65px 115px;
  max-width: 746px;
  background-color: #F6F6F5;
  opacity: 0;
  transform: perspective(600px) translate(0, -100%) rotateX(45deg);
  transition: all 0.8s ease 0s;
}
.modal__close {
  width: 30px;
  height: 30px;
  font-size: 20px;
  position: absolute;
  right: 26px;
  top: 26px;
  text-decoration: none;
}
.modal__close::after, .modal__close::before {
  position: absolute;
  content: "";
  width: 30px;
  border-top: 1px solid #69766D;
  top: 14px;
  left: 0;
  transform: rotate(45deg);
}
.modal__close::before {
  transform: rotate(-45deg);
}
.modal__title {
  font-size: 42px;
  line-height: 47px;
  margin-bottom: 50px;
}
.modal__title::after {
  position: absolute;
  content: url(../../img/svg/modal-decoration.svg);
  top: 28px;
  left: 138px;
}
.modal.open {
  z-index: 15;
  opacity: 1;
  visibility: visible;
}
.modal.open .modal__content {
  opacity: 1;
  transform: perspective(600px) translate(0, 0%) rotateX(0deg);
}

.form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 520px;
}
.form__heading {
  margin-bottom: 10px;
  font-family: "Lato-Light", sans-serif;
  font-weight: 300;
  font-size: 18px;
  line-height: 22px;
  cursor: pointer;
}
.form__number {
  margin-bottom: 10px;
}
.form__text {
  margin: 0;
  margin-bottom: 30px;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
}
.form__button {
  margin: 0 auto;
  cursor: pointer;
}